* {
  padding: 0;
  margin: 0;
}

.header{
  display: flex;
  padding: 20px 10px 10px 10px;
  justify-content: space-between;
  background-color: darkgray;
}

.logo{
  font-size: large;
  font-weight: 600;
  padding: 10px 10px 10px 10px;
}
.text{
  margin-left: 10px;
}

.userName{
  font-size: 25px;
  font-weight: 600;
  padding: 10px 10px 10px 10px;
}

.logoutButton{
  padding: 10px 10px 10px 10px;
}

.content{
  height: 71.8vh;
  overflow-y: scroll;
}

.footer{
  margin-top: 42px;
}

.custom-form{
  background: none;
  background-color: white;
}
.list{
  align-items: center;
}

/* transection page */
.filters {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 20px;
  box-shadow: 0 0 3px gray;
}

/* antd icons */
.switch-icons {
  border: 1px solid rgba(0, 0, 0, 0.677);
  border-radius: 5px;
  padding: 10px 15px;
}

.anticon svg {
  font-size: 20px;
  cursor: pointer;
}

.active-icon {
  color: black;
}
.inactive-icon {
  color: gray;
}

.register-page{
  max-width: 450px; 
  margin: 140px auto;
  border: 1.5px solid gray;
  border-radius: 10px;
  padding: 60px 40px;
  font-weight: 400;
  background-image: none;
  background-color: white;
}

.registration-container{
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.select-value{
  margin-right: 5px;
}

/* Card Related css */
.progress-circle{
  margin: 10px 5px;
}

.income-category{
  display: flex;
  flex-direction: column;
}

.category-header-income{
  margin: 0 0 20px 0;
  background-color: black;
  color: white;
  padding: 10px;
  border-radius: 5px;
}

.category-header-expense{
  margin: 0 0 20px 0;
  background-color: lightskyblue;
  padding: 10px;
  border-radius: 5px;
}